<template><section><h2>Alert 组件</h2>
<p>alert 组件</p>
<template>
  <button type="button" id="btn">click</button>
</template>
</section></template>

<script>
  export default {
    mounted () {
      document.getElementById('btn').addEventListener('click', function() {
        alert(123123)
      }, false)
    }
  }
</script>


